:root {
  --basic-black: #000000;
  --basic-dust: #D7DCDE;
  --basic-gray: #E1E1E1;
  --basic-green: #00CA74;
  --basic-red: #FB565A;
  --basic-yellow: #EFC849;
  --basic-white: #FFFFFF;

  --special-black: #283136;
  --special-dust: #B4B9BB;
  --special-gray: #666666;
  --special-green: #00BC6C;
  --special-red: #E74246;
  --special-yellow: #EAB534;
  --special-white: rgba(255, 255, 255, 0.3);

  --decorated-black: #444444;
  --decorated-green: #00AA62;
  --decorated-yellow: #E5A722;
  --decorated-red: #D7373B;
}

/* typography */

@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"),
    url("../fonts/roboto.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotomedium.woff2") format("woff2"),
    url("../fonts/robotomedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotobold.woff2") format("woff2"),
    url("../fonts/robotobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

/* tags */

body {
  width: 1160px;
  margin: 0 auto;
  padding: 0;

  color: var(--basic-black);
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  background-color: var(--basic-white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

/* common */

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list-reset {
  margin: 0;
  padding: 0;

  list-style: none;
}

/* action-button */

.action-button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  width: 160px;
  padding: 17px 0 15px;

  color: var(--basic-white);
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;

  cursor: pointer;
  background-color: var(--basic-red);
  border: none;
  border-radius: 3px;
}

.action-button:hover {
  background-color: var(--special-red);
}

.action-button:active,
.action-button:focus {
  color: var(--special-white);

  background-color: var(--decorated-red);
  outline: none;
}

.action-button--green {
  background-color: var(--basic-green);
}

.action-button--green:hover {
  background-color: var(--special-green);
}

.action-button--green:active,
.action-button--green:focus {
  background-color: var(--decorated-green);
}

.action-button--yellow {
  background-color: var(--basic-yellow);
}

.action-button--yellow:hover {
  background-color: var(--special-yellow);
}

.action-button--yellow:active,
.action-button--yellow:focus {
  background-color: var(--decorated-yellow);
}


/* main-header */

.main-header__navigation a {
  display: inline-block;

  font-weight: 500;
  line-height: 30px;
  text-transform: uppercase;
}

.main-header__navigation a:hover {
  color: var(--basic-red);
}

.main-header__navigation a:active,
.main-header__navigation a:focus {
  color: inherit;

  opacity: 0.3;
  outline: none;
}

.main-header__navigation a:not([href]):not(.main-header__logo)::after {
  content: "";

  display: block;
  width: 100%;
  height: 2px;

  background-color: var(--basic-red);
}

/* principles */

.principles__slide-title {
  font-weight: 500;
  font-size: 55px;
  line-height: 55px;
}

.principles__slide-description {
  color: var(--special-black);
}

.principles__slide-button {
  width: 240px;
}

/* services */

.services__servive-title {
  font-weight: 700;
  text-transform: uppercase;
}

.services__servive-description {
  color: var(--special-black);
}

/* about */

.about__title {
  font-weight: 500;
  font-size: 45px;
  line-height: 45px;
}

.about__description {
  color: var(--special-black);
}

.about__services-title {
  color: var(--special-black);
  font-weight: 700;
  text-transform: uppercase;
}

.about__services-list li {
  color: var(--special-black);
}

.about__properties-title {
  color: var(--special-black);
  font-weight: 700;
  text-transform: uppercase;
}

.about__properties-list dt {
  color: var(--special-black);
  line-height: 18px;
}

.about__properties-list dd {
  font-weight: 700;
  font-size: 45px;
  line-height: 64px;
}

.about__properties-list span {
  font-size: 26px;
  line-height: 40px;
  vertical-align: text-top;
}

/* partners */

.partners__link img {
  opacity: 0.2;
}

.partners__link:hover img {
  opacity: 1;
}

.partners__link:active img,
.partners__link:focus img {
  opacity: 0.1;
}

/* map */

.map__description {
  width: 319px;
  padding: 49px 50px 47px;

  background-color: var(--basic-white);
}

.map__description-title {
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
}

.map__description-address,
.map__description-telephone {
  color: var(--special-gray);
  line-height: 18px;
}

.map__description-button {
  width: 219px;
}

/* main-footer */

.main-footer__link {
  display: flex;
  justify-content: center;
  width: 81px;
  height: 81px;

  background-color: var(--basic-gray);
  border-radius: 50%;
}

.main-footer__link:hover {
  background-color: var(--special-red);
}

.main-footer__link:active,
.main-footer__link:focus {
  background-color: var(--decorated-red);
}

.main-footer__link:active img,
.main-footer__link:focus img {
  opacity: 0.3;
}

.main-footer__promotion-title {
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
}

.main-footer__promotion-description {
  color: var(--decorated-black);
  line-height: 22px;
}

/* popup */

.popup__title {
  font-weight: 700;
  font-size: 45px;
  line-height: 53px;
}

.popup__close-button {
  display: block;
  width: 21px;
  height: 21px;

  cursor: pointer;
  background-color: transparent;
  background-image: url("../img/close-cross.svg");
  border: none;
  opacity: 0.3;
}

.popup__close-button:hover {
  opacity: 1;
}

.popup__close-button:active,
.popup__close-button:focus {
  opacity: 0.1;
  outline: none;
}

.popup__field label {
  font-weight: bold;
  line-height: 18px;
}

.popup__field input,
.popup__field textarea {
  padding: 16px;

  line-height: 18px;

  background-color: var(--basic-white);
  border: 2px solid var(--basic-dust);
  border-radius: 3px;
}

.popup__field textarea {
  resize: none;
}

.popup__field input:hover,
.popup__field textarea:hover {
  border-color: var(--special-dust);
}

.popup__field input:invalid,
.popup__field textarea:invalid {
  border-color: var(--special-red);
}

.popup__field input::placeholder,
.popup__field textarea::placeholder {
  color: var(--decorated-black);

  opacity: 0.5;
}

.popup__field input:focus::placeholder,
.popup__field textarea:focus::placeholder {
  opacity: 1;
}

.popup__field input:invalid::placeholder,
.popup__field textarea:invalid::placeholder {
  color: var(--special-red);

  opacity: 1;
}

.popup__submit {
  width: 259px;
}
